*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  font-family: system-ui;
  font-size: 1.125rem;
  line-height: 1.6;
  overflow: hidden;
}
body {
  background: #141414;
  z-index: -3; 
  overflow-y: hidden;
}

.App {
  font-family: sans-serif;
  text-align: center;
  display: grid;
  place-items: center;
  padding: 1rem;
}

.card {
  width: 100%;
  max-width: 1000px;
  min-width: 400px;
  height: 250px;
  margin: 1rem 0;
  background: #292929;
  position: relative;
  z-index: 0;
  border-radius: 8px;
  overflow: hidden;
  padding: 1rem;
  text-align: justify;
  display: flex;
  justify-content: center;
  align-items: center;
}

.card-content {
  color: #e0e0e0;
  font-size: 0.75em;
  max-width: 90%;
}

@keyframes rotate {
  100% {
    transform: rotate(1turn);
  }
}

.card::before {
  content: "";
  position: absolute;
  z-index: -2;
  left: -50%;
  top: -50%;
  width: 200%;
  height: 200%;
  background-color: #1a232a;
  background-repeat: no-repeat;
  background-position: 0 0;
  background-image: conic-gradient(transparent, #e0e0e0, transparent 30%);
  animation: rotate 6s linear infinite;
}

.card::after {
  content: "";
  position: absolute;
  z-index: -1;
  left: 2px;
  top: 2px;
  width: calc(100% - 4px);
  height: calc(100% - 4px);
  background: #000;
  border-radius: 8px;
  animation: opacityChange 5s infinite linear;
}
img {
  max-width: 100%;
  display: block;
}
.wrapper {
  max-width: 50rem;
  margin-inline: auto;
  padding-inline: 1rem;
}

.accordion {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-top: 5rem;
  /* contain: content; */ 
  --_button-size: 3rem;
  --_panel-padding: 0.75rem;
  --_panel-gap: 1rem;
}
@media (min-width: 45em) {
  .accordion {
      flex-direction: row;
      height: 30rem;
  }
}
.accordion * {
  margin: 0;
}
.accordion-panel {
  position: relative;
  isolation: isolate;
  flex-basis: calc((var(--_panel-padding) * 2) + var(--_button-size));
  overflow: hidden;
  padding: var(--_panel-padding);
  padding-right: calc(var(--_panel-padding) * 4);
  border-radius: calc(((var(--_panel-padding) * 2) + var(--_button-size)) / 2);
  transition: flex-basis 500ms, flex-grow 500ms;
}
.accordion-panel:nth-child(1) {
  --_panel-color: hsl(188, 79%, 41%);
}
.accordion-panel:nth-child(2) {
  --_panel-color: hsl(125, 70%, 50%);
}
.accordion-panel:nth-child(3) {
  --_panel-color: pink;
}
.accordion-panel:nth-child(4) {
  --_panel-color: hsla(0, 2%, 82%, 0.5);
}
.accordion-panel:has([aria-expanded="true"]) {
  flex-basis: clamp(15rem, 40vh, 20rem);
  flex-grow: 1;
}
.accordion-trigger {
  outline: 0;
}
.accordion-panel:focus-within {
  outline: 3px solid var(--_panel-color);
  outline-offset: 4px;
}
.accordion-content > p {
  transform: translateY(2rem);
  opacity: 0;
  color: #e0e0e0;
  margin-left: calc(var(--_button-size) + var(--_panel-gap));
}
.accordion-panel:has([aria-expanded="true"]) p {
  transition: transform 500ms 500ms, opacity 500ms 500ms;
  transform: translateY(0);
  opacity: 1;
}
.accordion-title {
  font-size: 1.5rem;
  font-weight: 700;
  position: relative;
  isolation: isolate;
  display: grid;
  align-items: center;
}
@media (max-width: 44.999em) {
  .accordion-title::after {
      content: "";
      position: absolute;
      left: calc((var(--_panel-gap) + var(--_button-size)) * -1);
      width: calc(100% + (var(--_button-size) * 2));
      height: var(--_button-size);
      background: hsla(0, 2%, 82%, 0.5);
      z-index: -1;
      border-radius: 100vw;
  }
  #profile_pic {
    display: none;
  }
  #blob {
    display: none;
  }
  html, body {
    overflow: auto;
  }
}
@media (min-width: 44.999em) {
  .accordion-panel:has([aria-expanded="true"]) 
  .accordion-title::after {
      content: "";
      position: absolute;
      left: calc((var(--_panel-gap) + var(--_button-size)) * -1);
      width: calc(100% + (var(--_button-size) * 2));
      height: var(--_button-size);
      background: hsla(0, 2%, 82%, 0.5);
      z-index: -1;
      border-radius: 100vw;
  }
  .container {
    height: fit-content;
    width: calc(100vw-2em);
    display: grid;
    margin: 2em;
    grid-template-areas: 
    "profile-pic . accordion accordion"
    "root root accordion accordion"
    "root root accordion accordion";
    gap: 2em;
    padding: 1em;
  }
  .wrapper {
    grid-area: accordion;
  }
  #root {
    grid-area: root;
    justify-self: center;
  }
  #profile_pic {
    grid-area: profile-pic;
    margin: 0.75rem 0 0 1rem;
  }
}
.accordion-image {
  position: absolute;
  inset: 0;
  object-fit: cover;
  width: 100%;
  height: 100%;
  z-index: -1;
  transition: filter 500ms;
}
.accordion-panel:has([aria-expanded="true"]) .accordion-image {
  filter: brightness(0.5);
}
.accordion-trigger {
  background: transparent;
  border: 0;
  display: flex;
  align-items: center;
  gap: var(--_panel-gap);
  flex-direction: row-reverse;
  padding: 0;
}
.accordion-icon {
  fill: var(--_panel-color);
  background: hsl(0 0% 0% / 0.55);
  width: var(--_button-size);
  aspect-ratio: 1 / 1;
  padding: 0.75rem;
  border-radius: 50%;
  z-index: 1;
}

a::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 4px;
  border-radius: 4px;
  background-color: #e0e0e0;
  bottom: 0;
  left: 0;
  transform-origin: right;
  transform: scaleX(0);
  transition: transform .3s ease-in-out;
}

a:hover::before {
  transform-origin: left;
  transform: scaleX(1);
}

a {
text-decoration: none;
color: #e0e0e0;
font-weight: bold;
font-feature-settings: "dnom";
position: relative;
}
@keyframes rotate {
  from {
    rotate: 0deg;
  }
  
  50% {
    scale: 1 1.5;
  }
  
  to {
    rotate: 360deg;
  }
}
#blob {
  background-color: white;
  height: 30vmax;
  aspect-ratio: 1;
  position: absolute;
  left: 50%;
  top: 50%;
  translate: -50% -50%;
  border-radius: 50%;
  background: linear-gradient(to right, aquamarine, mediumpurple);
  animation: rotate 20s infinite;
  opacity: 0.8;
  z-index: -2;
}
#blur {
  height: 100%;
  width: 100%;
  position: absolute;
  z-index: -1;
  backdrop-filter: blur(12vmax);
  background-size: fit;
}
#profile_pic {
  width: 12rem;
  aspect-ratio: inherit;
  border-radius: 0.5rem;
  box-shadow: 0 0 0.75rem #e0e0e0;
}